//--|🠋| Scaling Layout |🠋|--//
@import '../Body.scss';

//--|🠋| Color Scheme (Résumé) |🠋|--//
$primary-dark: #1f3131; //--🠊 rgba(31,49,49,1) 🠈--//
$primary-medium: #3d6262; //--🠊 rgba(61,98,98,1) 🠈--//
$primary-light: #9eb0b0; //--🠊 rgba(158,176,176,1) 🠈--//

$secondary-dark: #230510; //--🠊 rgba(35,5,16,1) 🠈--//
$secondary-medium: #450920; //--🠊 rgba(69,9,32,1) 🠈--//
$secondary-light: #a2848f; //--🠊 rgba(162,132,143,1) 🠈--//

$tertiary-dark: #392319; //--🠊 rgba(57,35,25,1) 🠈--//
$tertiary-medium: #714631; //--🠊 rgba(113,70,49,1) 🠈--//
$tertiary-light: #b8a298; //--🠊 rgba(184,162,152,1) 🠈--//

//--|🠋| Backup Colors |🠋|--//
$green-neon: #00ff00; //--🠊 rgba(0, 255, 0, 1) 🠈--//
$green-dark: #274e13; //--🠊 rgba(39, 78, 19, 1) 🠈--//
$green-light: #b6d7a8; //--🠊 rgba(182, 215, 168, 1) 🠈--//

$pink-neon: #ff00ff; //--🠊 rgba(255, 0, 255, 1) 🠈--//
$pink-dark: #4c1130; //--🠊 rgba(76, 17, 48, 1) 🠈--//
$pink-light: #d5a6bd; //--🠊 rgba(213, 166, 189, 1) 🠈--//

$yellow-neon: #ffff00; //--🠊 rgba(255, 255, 0, 1) 🠈--//
$yellow-dark: #7f6000; //--🠊 rgba(127, 96, 0, 1) 🠈--//
$yellow-light: #ffe599; //--🠊 rgba(255, 229, 153, 1) 🠈--//

$orange-neon: #ff9900; //--🠊 rgba(255, 153, 0, 1) 🠈--//
$orange-dark: #783f04; //--🠊 rgba(120, 63, 4, 1) 🠈--//
$orange-light: #f9cb9c; //--🠊 rgba(249, 203, 156, 1) 🠈--//

$blue-neon: #3c90ff; //--🠊 rgba(0, 0, 255, 1) 🠈--//
$blue-dark: #073763; //--🠊 rgba(127, 17, 224, 1) 🠈--//
$blue-light: #9fc5e8; //--🠊 rgba(159, 197, 232, 1) 🠈--//

$purple-neon: #9900ff; //--🠊 rgba(153, 0, 255, 1) 🠈--//
$purple-dark: #20124d; //--🠊 rgba(32, 18, 77, 1) 🠈--//
$purple-light: #b4a7d6; //--🠊 rgba(180, 167, 214, 1) 🠈--//

$red-neon: #ff0000; //--🠊 rgba(255, 0, 0, 1) 🠈--//
$red-dark: #660000; //--🠊 rgba(102, 0, 0, 1) 🠈--//
$red-light: #ea9999; //--🠊 rgba(234, 153, 153, 1) 🠈--//

$brown-neon: #a97142; //--🠊 rgba(169, 113, 66, 1) 🠈--//
$brown-dark: #4f341d; //--🠊 rgba(79, 52, 29, 1) 🠈--//
$brown-light: #ffd3af; //--🠊 rgba(255, 211, 175, 1) 🠈--//

$grey-neon: #bec2cb; //--🠊 rgba(190, 194, 203, 1) 🠈--//
$grey-dark: #434343; //--🠊 rgba(67, 67, 67, 1) 🠈--//
$grey-light: #efefef; //--🠊 rgba(239, 239, 239, 1) 🠈--//

$turquoise-neon: #00ffff; //--🠊 rgba(0, 255, 255, 1) 🠈--//
$turquoise-dark: #0c343d; //--🠊 rgba(12, 52, 61, 1) 🠈--//
$turquoise-light: #a2c4c9; //--🠊 rgba(162, 196, 201, 1) 🠈--//
//--|🠉| Backup Colors |🠉|--//

div[class='resume-body'] {
  height: 100vh;
  display: grid;
  overflow-y: hidden;
  background: $primary-dark;
  main[id*='resume'] {
    grid-area: 1 / 1 / 4 / 4; //~~|🠊 row-start / col-start / row-end / col-end 🠈|~~//
    article:first-child {
      display: grid;
      overflow-y: auto;
      max-height: 100vh;
      &::-webkit-scrollbar {
        display: none; //~~|🠊 Hide Scrollbar 🠈|~~//
      }
    }
  }

  //--|🠋 Desktop (Landscape) 🠋|--//
  @media screen and (orientation: landscape) {
    grid-template-rows: 4rem 1fr 4rem;
    grid-template-columns: 6rem 1fr 6rem;
  }
  //--|🠋 Mobile (Portrait) 🠋|--//
  @media screen and (orientation: portrait) {
    grid-template-rows: 6rem 1fr 6rem;
    grid-template-columns: 1rem 1fr 1rem;
  }

  //--|🠋 Tablet (Square) 🠋|--//
  @media screen and (max-aspect-ratio: 16/9) and (min-aspect-ratio: 1/1) {
    display: none;
  }

  main[id*='resume'] {
    article::-webkit-scrollbar {
      display: none; //~~|🠊 Hide Scrollbar 🠈|~~//
    }
  }
}
